:before,
*:after {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: currentColor;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

button {
  font-family: inherit;
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}


b {
  /* used for event dates/times */
  margin-right: 3px;
}

.demo-app {
  display: flex;
  min-height: 100%;
  font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
  font-size: 14px;
}

.demo-app-sidebar {
  width: 300px;
  line-height: 1.5;
  background: #eaf9ff;
  border-right: 1px solid #d3e2e8;
}

.demo-app-sidebar-section {
  padding: 2em;
}

.demo-app-main {
  flex-grow: 1;
  padding: 3em;
}

.fc {
  /* the calendar root */
  max-width: 1100px;
  margin: 0 auto;
}

/* .fc-toolbar-title {
  text-transform: uppercase;
} */
.fc .fc-button-group>.fc-button {
  flex: 1 1 auto;
  position: relative;
  text-transform: uppercase;
}

.fc-toolbar-chunk {
  display: flex;
}

.fc-button.fc-button-primary {
  border-radius: 1.078em;
  background-color: #DCE3E5;
  border: 1px solid rgba(220, 227, 229, 0.8);
  border-radius: 8px;

}

.fc-dayGridMonth-button.fc-button.fc-button-primary.fc-button-active {
  background-color: #DCE3E5;
  border: 1px solid rgba(220, 227, 229, 0.8);
  border-radius: 8px;
}

.fc .fc-toolbar-title {
  font-size: 16px;
  line-height: calc(18/16);
  margin: 0;
  border-radius: 8px;
  /* border: 1px solid black; */
  background-color: #3E85F3;
  color: #FFFFFF;
  padding: 8px;
}

.fc-timeGridDay-button.fc-button.fc-button-primary {
  background-color: #E3F3FF;
  font-size: 16px;
  line-height: calc(18/16);
  border-radius: 8px;
}

.fc-dayGridMonth-button.fc-button.fc-button-primary.fc-button-active {
  background-color: #E3F3FF;
  font-size: 16px;
  line-height: calc(18/16);
}


.fc-direction-ltr .fc-button-group>.fc-button:not(:first-child),
.fc-direction-ltr .fc-button-group>.fc-button:not(:last-child) {
  border-radius: 8px;
  margin-left: 0px;
}


.fc-today-button.fc-button.fc-button-primary:disabled {
  background-color: var(--fc-button-bg-color);
  border-color: var(--fc-button-border-color);
  color: var(--fc-button-text-color);
}

.fc-today-button.fc-button.fc-button-primary:not(:disabled) {
  background-color: #3E85F3;
}

.fc-dayGridMonth-button.fc-button.fc-button-primary.fc-button-active:hover,
.fc-timeGridDay-button.fc-button.fc-button-primary:hover {
  background-color: #CAE8FF;
  color: #3E85F3;
  border-color: transparent;
}

